import React, { useEffect, useState } from "react";
import HeaderTopCommon from "../../components/common/HeaderTop";
import { WrapperContainer } from "../../style-App";
import { useDispatch } from "react-redux";
import HorizontalSidebar from "../../components/horizontal-sidebar";
import VerticalSidebar from "../../components/vertical-sidebar";
import { GetOrders } from "../../redux/orders";
import GottenOrdersComponent from "../../components/gotten-orders";

function Orders() {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(GetOrders());
  }, []);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  return window.localStorage.getItem("checked") == "false" ? (
    <HorizontalSidebar>
      <WrapperContainer style={{ marginTop: "112px" }}>
        <HeaderTopCommon isBtn={false} title={"Заказы"} />
        <GottenOrdersComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </HorizontalSidebar>
  ) : (
    <VerticalSidebar>
      <WrapperContainer>
        <HeaderTopCommon isBtn={false} title={"Заказы"} />
        <GottenOrdersComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </VerticalSidebar>
  );
}
export default Orders;
