import * as React from "react";
import { useSelector } from "react-redux";
import TableCommon from "../../common/table";
import "./styles.css";
import { Image, Popover, Space } from "antd";
import Cookies from "universal-cookie";
const cookies = new Cookies();
export default function GottenOrdersTable({ HandleDelete, handlePutModal }) {
  const brandsGetState = useSelector((state) => state.orders);
  const row = brandsGetState.OrdersGet?.data;

  const rows = row.filter((elem) => elem.admins.id == cookies.get("adminId"));

  const data = [];
  rows?.map((elem, index) => {
    data.push({
      data: index + 1,
      key: elem.id,
      Типплатежа: elem.type_payment,
      Типпродажи: elem.type_sell,
      Местонахождение: elem.location,
      Цена: elem.price,
      Действия: (
        <div className="boxx">
          <div className="btn-wraps">
            <button onClick={handlePutModal} id={elem.id}>
              <i id={elem.id} class="bx bx-arrow-to-right"></i>
            </button>
          </div>
        </div>
      ),
    });
  });

  const columns = [
    {
      title: "№",
      dataIndex: "data",
      key: "data",
      width: 50,
      align: "center",
    },
    {
      title: "Тип платежа",
      dataIndex: "Типплатежа",
      key: "Типплатежа",
    },
    {
      title: "Тип продажи",
      dataIndex: "Типпродажи",
      key: "Типпродажи",
    },
    {
      title: "Местонахождение",
      dataIndex: "Местонахождение",
      key: "Местонахождение",
    },
    {
      title: "Цена",
      dataIndex: "Цена",
      key: "Цена",
    },
    {
      title: "Действия",
      dataIndex: "Действия",
      key: "Действия",
      align: "center",
    },
  ];
  return (
    <TableCommon
      bordered
      columns={columns}
      data={data}
      pagination={false}
      scroll={{
        y: 330,
      }}
    />
  );
}
