import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { Image, Input, Spin, Tooltip } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import "./styles.css";
import { PutOrders } from "../../../redux/orders";
import Cookies from "universal-cookie";
const cookies = new Cookies();

function GottenOrdersPut({ open, HandleClose, put_id }) {
  const id = put_id;
  const dispatch = useDispatch();
  const [nameRu, setNameRu] = useState();

  const OrdersData = useSelector((state) => state.orders.OrdersGet.data);

  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      status: nameRu,
      admins: cookies.get("adminId"),
    };
    await dispatch(PutOrders({ id: id, body }));
    window.location.reload();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );
  const DateFormat = (date) => {
    var d = new Date(date),
      month = "" + (d.getMonth() + 1),
      day = "" + d.getDate(),
      year = d.getFullYear(),
      hours = "" + d.getHours(),
      minutes = "" + d.getMinutes();

    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;
    if (9 < hours.length) {
      hours = "0" + hours;
    } else {
      hours = "" + hours;
    }
    if (minutes.length < 10) minutes = "0" + minutes;

    return [day, "/", month, "/", year, " ", hours, ":", minutes];
  };

  const options = [
    {
      label: "в процессе",
      value: "в процессе",
    },
    {
      label: "выполнено",
      value: "выполнено",
    },
    {
      label: "не оплачено",
      value: "не оплачено",
    },
    {
      label: "оплачено",
      value: "оплачено",
    },
    {
      label: "отказано",
      value: "отказано",
    },
  ];

  const HandelChnage = (e) => {
    setNameRu(e);
  };

  const getNextMonths = () => {
    const currentDate = new Date(
      `${OrdersData.map((el) => (el.id == id ? el.createdAt : null))}`
    );
    const months = [];

    for (let i = 0; i < 6; i++) {
      const nextMonth = new Date(
        currentDate.getFullYear(),
        currentDate.getMonth() + i + 1,
        1
      );
      months.push(
        nextMonth.toLocaleDateString("ru", {
          month: "short",
          year: "numeric",
        })
      );
    }

    return months;
  };

  const nextMonths = getNextMonths();
  return (
    <DrawerCommon title={"Изменить заказ"} open={open} onClose={HandleClose}>
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div className="input_wrap">
            <Row className="row">
              {OrdersData.map((elem) =>
                elem.id == id ? (
                  <>
                    <Col lg={6}>
                      <h2>Данные о товаре</h2>
                      <div className="proInfo">
                        <div className="products">
                          {elem.products.map((ele) => (
                            <>
                              <div className="boxPro">
                                <Image
                                  width={60}
                                  height="100%"
                                  style={{
                                    aspectRatio: "1 / 1",
                                    borderRadius: "20px",
                                    objectFit: "cover",
                                  }}
                                  src={elem.image}
                                />
                                <div className="texts">
                                  <h3>{ele.product_name_ru}</h3>
                                  <p>{ele.price} uzs</p>
                                </div>
                              </div>
                            </>
                          ))}
                        </div>
                        <div className="infobox">
                          <div className="actions">
                            {elem.status == "ожидание" ? (
                              <>
                                <button className="oj">{elem.status}</button>
                              </>
                            ) : elem.status == "в процессе" ? (
                              <>
                                <button className="vp">{elem.status}</button>
                              </>
                            ) : elem.status == "выполнено" ? (
                              <>
                                <button className="vip">{elem.status}</button>
                              </>
                            ) : elem.status == "не оплачено" ? (
                              <>
                                <button className="nep">{elem.status}</button>
                              </>
                            ) : elem.status == "оплачено" ? (
                              <>
                                <button className="op">{elem.status}</button>
                              </>
                            ) : elem.status == "отказано" ? (
                              <>
                                <button className="ot">{elem.status}</button>
                              </>
                            ) : null}
                          </div>
                          <div className="infos">
                            <h3>Номер заказ : №{elem.id}</h3>
                            <h3>Тип оплаты : {elem.type_payment}</h3>
                            <h3>Тип заказа : {elem.type_sell}</h3>
                            <h3>Время заказа : {DateFormat(elem.createdAt)}</h3>
                          </div>
                        </div>
                      </div>
                    </Col>
                    <Col lg={6}>
                      <h2>Данные покупателя</h2>
                      <div className="userinfo">
                        <h3>
                          Имя и фамилия : {elem.users.surname} {elem.users.name}
                        </h3>
                        <h3>номер телефона : {elem.users.phone}</h3>
                        <h3>Доп-номер телефона : {elem.addition_phone}</h3>
                        <h3>адресс на карте : {elem.location}</h3>
                        <h3>Коментария : </h3>
                        <p>{elem.comentary}</p>
                      </div>
                    </Col>
                    <Col lg={3}>
                      <br />
                      <br />
                      <SelectCommon
                        onChange={HandelChnage}
                        placeholder={"Выберите статус"}
                        options={options}
                      />
                    </Col>
                    <Col lg={12}>
                      <ul className="ull">
                        {nextMonths.map((month, index, ele) => (
                          <li key={index}>
                            <Tooltip
                              title={
                                <button
                                  className="btn"
                                  onClick={async () => {
                                    const thing = elem.id + month + index;
                                    await cookies.set(
                                      `${thing
                                        .replace(/\s+/g, "")
                                        .replace(/[^0-9]/g, "")}`,
                                      "active"
                                    );
                                    return window.location.reload();
                                  }}
                                >
                                  Отметить оплаченным
                                </button>
                              }
                              trigger="hover"
                            >
                              <button
                                className={cookies.get(
                                  `${
                                    elem.id +
                                    month
                                      .replace(/\s+/g, "")
                                      .replace(/[^0-9]/g, "") +
                                    index
                                  }`
                                )}
                              >
                                {month}
                              </button>
                            </Tooltip>
                          </li>
                        ))}
                      </ul>
                    </Col>
                  </>
                ) : null
              )}
            </Row>
          </div>

          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Изменить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default GottenOrdersPut;
