import React from "react";
import GottenOrdersTable from "./table";
import GottenOrdersPut from "./put";
import { useDispatch } from "react-redux";
import { useState } from "react";

function GottenOrdersComponent() {
  const dispatch = useDispatch();
  const [adminId, setAdminId] = useState();
  const [openDelete, setOpenDelete] = useState(false);
  const handleCloseDelete = () => setOpenDelete(false);

  const handleDeleteModal = (e) => {
    setOpenDelete(true);
  };

  const [openPut, setOpenPut] = useState(false);

  const onClosePut = () => {
    setOpenPut(false);
  };

  const handlePutModal = async (e) => {
    setOpenPut(true);
    setAdminId(e.currentTarget.id);
  };
  return (
    <>
      <GottenOrdersTable handlePutModal={handlePutModal} />
      <GottenOrdersPut
        put_id={adminId}
        open={openPut}
        HandleClose={onClosePut}
      />
    </>
  );
}

export default GottenOrdersComponent;
